body {
  background-color: #f7f8fa;
}

.add-pro-page {
  width: 100vw;
  height: 100vh;
  position: relative;

  .back {
    width: 24px;
    height: 24px;
    font-size: 24px;
    position: absolute;
    left: 20px;
    top: 20px;
    cursor: pointer;
  }

  .create-type {
    width: 970px;
    height: 440px;
    position: fixed;
    left: 50%;
    top: 180px;
    transform: translate(-50%);

    .title {
      text-align: center;
      font-size: 20px;
      margin-bottom: 50px;
    }

    .content {
      display: flex;
      padding: 0 60px;
      justify-content: space-between;

      .blank,
      .copy {
        float: left;
        width: 400px;
        height: 240px;
        background-color: white;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .08);
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
          cursor: pointer;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .08), 4px 6px 12px 0 rgba(0, 0, 0, .08);
          ;
        }

        h3 {
          width: 140px;
          height: 100%;
          margin: 0;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          img {
            width: 50px;
            height: 50px;
          }

          p {
            margin-top: 10px;
            margin-bottom: 0;

            &:last-child {
              margin-top: 5px;
              font-size: 12px;
              color: #707070;
              font-weight: normal;
            }
          }
        }
      }
    }

  }
}

.exam-select {
  overflow: hidden;

  li {
    float: left;
    width: calc((100% - 20px) / 3);
    height: 108px;
    border: 1px solid #eaeaea;
    margin-bottom: 10px;
    margin-right: 10px;
    padding: 13px 18px;
    position: relative;
    cursor: pointer;

    &:hover {
      box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.12);
      ;
      .float{
        display: block;
      }
    }

    .float {
      position: absolute;
      display: none;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      line-height: 108px;
      text-align: center;
      background-color: rgba(255, 255, 255, 0.9);
      color: #2672ff;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }

    .question_count {
      position: absolute;
      left: 20px;
      bottom: 5px;
    }
  }
}